import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const Autorization = {
  state: () => ({
    autoriz: false,
  }),
  mutations: {
    autorization(state) {
      state.autoriz = true;
      localStorage.autoriz = true;
      // console.log(localStorage.autoriz);
    },
    deautorization(state) {
      state.autoriz = false;
      localStorage.autoriz = false;
    },
  },
  actions: {},
  getters: {},
};

const DataForNewTable = {
  state: () => ({
    groups: [
    ],
    subjects: [
      'Предметик',
      'Предметушка',
    ],
    selectSubject: '',
    selectDate: '',
  }),
  mutations: {
    remote_group(state, index) {
      state.groups.splice(index, 1);
    },
    new_group(state, newGroup) {
      state.groups.push(newGroup);
    },
    select_sub_and_date(state, payload) {
      state.selectDate = payload.selDate;
      state.selectSubject = payload.selSub;
    },
  },
  actions: {
  },
  getters: {},
};

const DataForCheckTable = {
  state: () => ({}),
  mutations: {},
  actions: {},
  getters: {},
};

export default new Vuex.Store({
  state: {
    count: 1,
  },
  mutations: {
    increment(state) {
      state.count += 1;
    },
  },
  actions: {
  },
  modules: {
    a: Autorization,
    n: DataForNewTable,
    c: DataForCheckTable,
  },
});
